import React, { Component,Fragment } from 'react';
import { AppBar, Toolbar, Button } from '@material-ui/core';
import About from './components/About';
import Home from './components/Home';
import Portfolio from './components/Portfolio';
import Contact from './components/Contact';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      display: 1,
    }

    this.renderHomeOnLoad = this.renderHomeOnLoad.bind(this);
  }

  renderHomeOnLoad() {
    if(this.state.display === 1) {
      this.setState({ display: 0});
      return <Home />;
    }  

  }

  render() {
    return (
      <Fragment>
        <Router>
          <div>
            <AppBar>
              <Toolbar>
                  <Link to='/react-portfolio/home'><Button>Home</Button></Link>
                  <Link to='/react-portfolio/about'><Button>About</Button></Link>
                  <Link to='/react-portfolio/portfolio'><Button>Portfolio</Button></Link>
                  <Link to='/react-portfolio/contact'><Button>Contact</Button></Link>
              </Toolbar>
            </AppBar>
            <Route exact path='/react-portfolio/home' component={Home} />
            <Route path='/react-portfolio/about' component={About} />
            <Route path='/react-portfolio/portfolio' component={Portfolio} />
            <Route path='/react-portfolio/contact' component={Contact} />
          </div>
        </Router>
          {this.renderHomeOnLoad()}
      </Fragment>
    );
  }
}

export default App;
