import React, { Component, Fragment } from 'react';
import { Grid, TextField, Paper, Button, Typography, Link } from '@material-ui/core';

const style = {
	grid: {
		margin: 'auto',
		marginTop: 80,
	},
	textField: {
		width: '100%',
	},
	paper: {
		padding: 40,
	}
}

class Contact extends Component {
	constructor(props) {
		super(props);
		this.state = {
			name: '',
			email: '',
			message: '',
			submit: "mailto:daniel_andric97@hotmail.com&body=Dear Daniel my name is ",		
		};

		this.handleReset = this.handleReset.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(e) {
		this.setState({ [e.target.name]: e.target.value });
	}

	handleReset() {
		this.setState({name: '', email: '', message: ''});
	}

	handleSubmit() {
		if(this.state.name && this.state.email && this.state.message != '') {
			return this.state.submit + this.state.name + ', ' + this.state.message + ' ,my email is: ' + this.state.email + ' THIS WAS SENT THROUGH THE CONTACT FORM';
		}
	}

	render() {
		return (
	        <Fragment>		      
				<Grid container>
					<Grid item style={style.grid} xs={12} md={8} lg={6}>
						<Paper style={style.paper}>
							<Typography variant='h4' color='primary'>Contact</Typography>
		    				<TextField
					          id="name"
					          label="Name"
					          margin="normal"
					          style={style.textField}
					          name='name'
					          onChange={this.handleChange}
					          value={this.state.name}
					          required
					        /><br/>
					        <TextField
					          id="email"
					          type='email'
					          label="Email"
					          margin="normal"
					          style={style.textField}
					          name='email'
					          onChange={this.handleChange}
					          value={this.state.email}
					          required
					        /><br/>
					        <TextField
					          id="message"
					          multiline
					          rows='5'
					          label="Message"
					          margin="normal"
					          style={style.textField}
					          name='message'
					          onChange={this.handleChange}
					          value={this.state.message}
					          required
					        />
					        <Link href={this.handleSubmit()} target="_top"><Button color='primary'>Submit</Button></Link>
					        <Button onClick={this.handleReset} color='primary'>Reset</Button>
					    </Paper>
					</Grid>		
				</Grid>	     
			</Fragment>
			)
	}
}
export default Contact;