import React, { Component,Fragment } from 'react';
import { Paper,	Grid,	Typography,	Card,	CardActions,	CardActionArea,	CardMedia,	CardContent,	Button,	Link } from '@material-ui/core';

const style = {
	Card: {
		width: 350,
		margin: 50,
	},
	grid: {
		marginTop: 80,
	}
}

class Portfolio extends Component {
	render() {
		return (
			<Fragment>
		        <Grid 
		        	id="Portfolio"
		        	container
		        	justify='center'
		        	style={style.grid}
		        >
	            <Grid item xs={12} style={{textAlign: 'center'}}>
	            	<Typography variant='h4' color='primary'>Portfolio</Typography>
	            </Grid>

	        	{/* Javascript Calculator (jQuery) */}

	            <Card style={style.Card}>
			      <CardActionArea>
			        <CardMedia
			          component="img"
			          alt="Contemplative Reptile"
			          height="140"
			          image="https://www.nationalgeographic.com/content/dam/animals/pictures/hero/reptiles-hero.adapt.1900.1.jpg"
			          title="Contemplative Reptile"
			        />
			        <CardContent>
			          <Typography gutterBottom variant="title" component="h2">
			            JavaScript Calculator (jQuery)
			          </Typography>
			        </CardContent>
			      </CardActionArea>
			      <CardActions>
			      	<Link href='https://github.com/LsbProxy/js-calculator' target='_blank'>
				        <Button size="small" color="primary">
				          Github
				        </Button>
				    </Link>
				    <Link href='https://lsbproxy.github.io/js-calculator/' target='_blank'>
				        <Button size="small" color="primary">
				          Live Demo
				        </Button>
				    </Link>
			      </CardActions>
			    </Card>

				{/* FCC Drum Machine */}

	            <Card style={style.Card}>
			      <CardActionArea>
			        <CardMedia
			          component="img"
			          alt="Contemplative Reptile"
			          height="140"
			          image="https://www.nationalgeographic.com/content/dam/animals/pictures/hero/reptiles-hero.adapt.1900.1.jpg"
			          title="Contemplative Reptile"
			        />
			        <CardContent>
			          <Typography gutterBottom variant="title" component="h2">
			            FCC Drum Machine (React.js)
			          </Typography>
			        </CardContent>
			      </CardActionArea>
			      <CardActions>
			      	<Link href='https://github.com/LsbProxy/fcc-drum-machine' target='_blank'>
				        <Button size="small" color="primary">
				          Github
				        </Button>
				    </Link>
				    <Link href='https://lsbproxy.github.io/fcc-drum-machine/' target='_blank'>
				        <Button size="small" color="primary">
				          Live Demo
				        </Button>
				    </Link>
			      </CardActions>
			    </Card>

				{/* Free Code Camp Local Weather App (jQuery) */}

	            <Card style={style.Card}>
			      <CardActionArea>
			        <CardMedia
			          component="img"
			          alt="Contemplative Reptile"
			          height="140"
			          image="https://www.nationalgeographic.com/content/dam/animals/pictures/hero/reptiles-hero.adapt.1900.1.jpg"
			          title="Contemplative Reptile"
			        />
			        <CardContent>
			          <Typography gutterBottom variant="title" component="h2">
			            FCC Local Weather App (jQuery)
			          </Typography>
			        </CardContent>
			      </CardActionArea>
			      <CardActions>
			      	<Link href='https://github.com/LsbProxy/fcc-weather-app' target='_blank'>
				        <Button size="small" color="primary">
				          Github
				        </Button>
				    </Link>
				    <Link href='https://lsbproxy.github.io/fcc-weather-app/' target='_blank'>
				        <Button size="small" color="primary">
				          Live Demo
				        </Button>
			        </Link>
			      </CardActions>
			    </Card>
				</Grid>			
			</Fragment>
			)
	}	
}
export default Portfolio;