import React, { Component, Fragment } from 'react';
import { Paper, Grid, Typography } from '@material-ui/core';

const style = {
	paper: {
		textAlign: 'center',
		padding: 40,
		marginTop: 80,
	},
	icon: {
		fontSize: '50px',
		color: '#1de9b6',
		textDecoration: 'none',
	}
}

class About extends Component {
	render() {
		return (
			<Fragment>
				<Grid container justify='center'>
			        <Grid item lg={10} xs={12}>
			            <Paper style={style.paper}>
		            		<Typography variant='h4' paragraph color='primary'>About</Typography>
		            		<Typography paragraph variant='h6'>I work with HTML, CSS, JavaScript, React, jQuery, Bootstrap, Material-UI</Typography>
		            		<a href="https://github.com/LsbProxy" target="_blank" style={style.icon}><i className="devicon-github-plain-wordmark"></i></a>
		            	</Paper>
		            </Grid>			
				</Grid>		
			</Fragment>
			)
	}	
}
export default About;